import { FC, ReactElement, useEffect, useState } from 'react';
import { Layout, Progress, Row, Col, Space, Menu } from 'antd';
import USER from 'constants/user';
import {
  CaretLeftOutlined,
  CaretRightOutlined,
  MailFilled,
  InfoCircleFilled,
  SignalFilled,
  StarFilled
} from '@ant-design/icons';

import { ProfileImg, ProgressWrapper } from './style';
import SidebarCard from 'components/SidebarCard';
import ReferenceLink from 'components/ReferenceLink';
import LanguageBar from 'components/LanguageBar';

const { Sider } = Layout;

type Props = {};

const Sidebar: FC<Props> = (): ReactElement => {
  const [collapsed, setCollapsed] = useState(false);

  useEffect(() => {
    document
      .getElementById('root')
      ?.classList[collapsed ? 'add' : 'remove']('collapsed');
  }, [collapsed]);

  return (
    <Sider
      trigger={null}
      collapsible
      collapsed={collapsed}
      style={{
        padding: '20px 20px 10px',
        backgroundColor: 'var(--color-5)'
      }}
      width="33%"
    >
      <Menu
        style={{
          background: 'none',
          height: '100%',
          display: 'flex',
          flexDirection: 'column'
        }}
        selectable={false}
        theme="dark"
      >
        <Menu.Item
          key="0"
          onClick={() => {
            setCollapsed(val => !val);
          }}
          style={{
            background: 'transparent',
            color: 'var(--color-1)',
            display: 'flex',
            justifyContent: 'flex-end',
            alignItems: 'center'
          }}
          icon={
            collapsed ? (
              <CaretRightOutlined />
            ) : (
              <CaretLeftOutlined style={{ fontSize: 24 }} />
            )
          }
        />
        <Menu.Item
          key="1"
          {...(collapsed && {
            icon: (
              <ProfileImg
                size={32}
                style={{ display: 'flex', justifyContent: 'center' }}
              />
            )
          })}
          style={{ height: 'unset' }}
        >
          <ProgressWrapper>
            <Progress
              type="circle"
              percent={100}
              width={200}
              strokeColor="var(--color-3)"
              trailColor="var(--color-1)"
              strokeWidth={5}
              showInfo={false}
            />
            <ProfileImg size={150} />
          </ProgressWrapper>
        </Menu.Item>
        <Menu.Item
          key="2"
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'flex-start',
            gap: '8px',
            height: '50px',
            marginTop: '48px',
            pointerEvents: 'none'
          }}
          {...(collapsed && {
            icon: <InfoCircleFilled style={{ color: 'var(--color-1)' }} />
          })}
        >
          <Col
            span={12}
            style={{
              fontSize: '32px',
              color: 'var(--color-2)',
              fontFamily: 'Inter',
              lineHeight: '20px',
              letterSpacing: '-0.17px'
            }}
          >
            {USER.personal.name}
          </Col>
          <Col span={12} style={{ fontSize: '18px' }}>
            {USER.personal.position}  {USER.personal.residence}
          </Col>
        </Menu.Item>
        <Menu.Item
          key="3"
          style={{ height: 'unset', marginTop: 24 }}
          {...(collapsed && {
            icon: <MailFilled style={{ color: 'var(--color-1)' }} />
          })}
        >
          <SidebarCard>
            <Row
              key={USER.contact.email.id}
              wrap={false}
              style={{
                justifyContent: 'space-between',
                alignItems: 'baseline'
              }}
            >
              <Col
                span={8}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  fontSize: '16px'
                }}
              >
                {USER.contact.email.icon}
                <a
                  style={{ marginLeft: 12 }}
                  href={`${USER.contact.email.type}:${USER.contact.email.rawValue}`}
                >
                  {USER.contact.email.value}
                </a>
              </Col>
              <ReferenceLink
                key={USER.contact.telegram.id}
                {...USER.contact.telegram}
              />
            </Row>
            <Row
              key={USER.contact.mobile.id}
              wrap={false}
              style={{
                justifyContent: 'space-between',
                alignItems: 'baseline'
              }}
            >
              <Col
                span={8}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  fontSize: '16px'
                }}
              >
                {USER.contact.mobile.icon}
                <a
                  style={{ marginLeft: 12 }}
                  href={`${USER.contact.mobile.type}:${USER.contact.mobile.rawValue}`}
                >
                  {USER.contact.mobile.value}
                </a>
              </Col>
              <ReferenceLink
                key={USER.contact.linkedin.id}
                {...USER.contact.linkedin}
              />
            </Row>
            <Row
              key={USER.contact.mobile.id}
              wrap={false}
              style={{
                justifyContent: 'space-between',
                alignItems: 'baseline'
              }}
            >
              <ReferenceLink
                key={USER.contact.github.id}
                {...USER.contact.github}
              />
            </Row>
          </SidebarCard>
        </Menu.Item>
        <Menu.Item
          key="4"
          style={{ height: 'unset', marginTop: 24 }}
          {...(collapsed && {
            icon: <SignalFilled style={{ color: 'var(--color-1)' }} />
          })}
        >
          <SidebarCard title="Language">
            <LanguageBar
              key={USER.languages.armenian.id}
              {...USER.languages.armenian}
            />
            <LanguageBar
              key={USER.languages.russian.id}
              {...USER.languages.russian}
            />
            <LanguageBar
              key={USER.languages.english.id}
              {...USER.languages.english}
            />
          </SidebarCard>
        </Menu.Item>
        <Menu.Item
          key="5"
          style={{ height: 'unset', flex: '1', marginTop: 24 }}
          {...(collapsed && {
            icon: <StarFilled style={{ color: 'var(--color-1)' }} />
          })}
        >
          <SidebarCard title="Hobbies">
            <Space
              direction="vertical"
              size="middle"
              style={{ width: '100%', marginTop: 24 }}
            >
              <ReferenceLink
                key={USER.hobbies.exercising.id}
                {...USER.hobbies.exercising}
                uppercase
              />
              <ReferenceLink
                key={USER.hobbies.cars.id}
                {...USER.hobbies.cars}
                uppercase
              />
              <ReferenceLink
                key={USER.hobbies.music.id}
                {...USER.hobbies.music}
                uppercase
              />
              <ReferenceLink
                key={USER.hobbies.travel.id}
                {...USER.hobbies.travel}
                uppercase
              />
            </Space>
          </SidebarCard>
        </Menu.Item>
        <Menu.Item
          key="6"
          onClick={() => {
            setCollapsed(val => !val);
          }}
          style={{
            background: 'transparent',
            color: 'var(--color-1)',
            display: 'flex',
            justifyContent: 'flex-end',
            alignItems: 'center'
          }}
          icon={
            collapsed ? (
              <CaretRightOutlined />
            ) : (
              <CaretLeftOutlined style={{ fontSize: 24 }} />
            )
          }
        />
      </Menu>
    </Sider>
  );
};

export default Sidebar;
