import { FC, ReactElement, useState } from 'react';
import { Drawer } from 'antd';

import { Github } from '../../icons';
import { Link, Wrapper, HeaderWrapper } from './style';

type Props = {
  icon?: ReactElement;
  projectLink?: string;
  name?: string;
  website?: string;
};

const ProjectLink: FC<Props> = ({
  icon,
  projectLink,
  name,
  website
}): ReactElement => {
  const [open, setOpen] = useState(false);

  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Wrapper onClick={showDrawer}>
        {icon}
        <div style={{ marginTop: 10, whiteSpace: 'nowrap' }}>{name}</div>
      </Wrapper>
      <Drawer
        height="100%"
        placement="bottom"
        onClose={onClose}
        visible={open}
        title={
          <HeaderWrapper>
            <Link href={projectLink} target="_blank">
              <Github style={{ fontSize: 32 }} />
            </Link>
            <div>{name}</div>
          </HeaderWrapper>
        }
      >
        <iframe
          src={website}
          width="100%"
          height="99%"
          style={{ border: 'none' }}
        />
      </Drawer>
    </>
  );
};

export default ProjectLink;
